@import url(https://fonts.googleapis.com/css?family=Lora&display=swap);
.results{width:100%;display:grid;grid-template-columns:0.3fr 0.7fr;grid-gap:1rem;gap:1rem;padding:1rem}.results *{margin:0;padding:0}.results__img img{width:100%}.results__content{display:flex;flex-direction:column;align-items:center}.results__content__title{font-size:1.5rem;font-weight:700}.results__content__dir{font-size:.8rem}.results__content__data{margin-top:0.5rem}.results__content__plot{margin-top:1rem;width:80%;font-family:"Lora", serif;font-style:italic}.results__content__rating{margin-top:2rem;width:100%;display:flex;justify-content:space-evenly}.rating__card__icon{display:inline-block;width:45px;height:45px}.rating__card__icon.i0{background:url(/omdb_interface/static/media/imdb.3d402771.png) no-repeat center center/cover}.rating__card__icon.i1{background:url(/omdb_interface/static/media/rotten_tomatoes.325ad745.jpg) no-repeat center center/cover}.rating__card__icon.i2{background:url(/omdb_interface/static/media/metacritic.c9c11cc5.png) no-repeat center center/cover}.rating__card__value{text-align:center;text-justify:center;font-weight:700;font-size:1.2rem;color:#11998e}@media (max-width: 500px){.results{grid-template-columns:1fr}}

*{margin:0;padding:0;box-sizing:border-box}body{font-family:"Lato", sans-serif}h1{font-size:1.6rem}h3{font-size:1.2rem}small{font-size:.8rem}.container{width:90%;max-width:850px;min-width:400px;margin:auto;padding:0.5rem 1rem}

.nav{background:#11998e;color:#fff}.nav__container{padding:0.5rem 1rem;width:90%;max-width:850px;min-width:400px;margin:auto}

.input{width:100%;padding:0.5rem 0}.input__form{width:100%;display:flex;flex-wrap:wrap}.input__form__item{margin:0.5rem 1rem;justify-self:center}.input__form__item input{width:40vw;min-width:300px;border:none;border-bottom:1px solid gray;height:30px;outline:0}.input__form__item input:focus{border-bottom:2px solid;border-color:#38ef7d}.input__form__item input:focus::-webkit-input-placeholder{color:transparent}.input__form__item input:focus:-ms-input-placeholder{color:transparent}.input__form__item input:focus::-ms-input-placeholder{color:transparent}.input__form__item input:focus::placeholder{color:transparent}.input__form__item select{margin:0.5rem 0;height:25px;width:100px;border:none;border-bottom:1px solid #333}.input__form__item select:focus{outline:2px solid #38ef7d}.input__submit{display:inline-block;padding:0.5rem 2rem;background:#11998e;color:#fff;cursor:pointer;border-radius:4px;font-weight:700;margin:0rem 1rem}

